body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

/*
* PERSONAL CHANGES
*/

.button-xsmall {
    font-size: 70%;
}

.is-left {
    text-align: left;
    margin-left: 10px;
}

.is-right {
    text-align: right;
    margin-right: 10px;
}

.portrait img {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background-size: cover;
    -webkit-background-size: cover;
    -moz-background-size: cover;
}

.portrait-mobile img {
    width: 150px;
    height: 300px;
    align-items: right;
    background-size: cover;
    -webkit-background-size: cover;
    -moz-background-size: cover;
}

.portrait-web img {
    width: 350px;
    height: 150px;
    align-items: right;
    background-size: cover;
    -webkit-background-size: cover;
    -moz-background-size: cover;
}

.portrait-desktop img {
    width: 200px;
    height: 100px;
    align-items: right;
    background-size: cover;
    -webkit-background-size: cover;
    -moz-background-size: cover;
}

.menu-horizontal {
    width: 100%;
}

.menu-list li {
    display: inline-block;
    text-align: center;
}

@media (min-width: 768px) {
    .portrait-desktop img {
    width: 400px;
    height: 200px;
    align-items: right;
    background-size: cover;
    -webkit-background-size: cover;
    -moz-background-size: cover;
    }

    .portrait-web img {
    width: 630px;
    height: 300px;
    align-items: right;
    background-size: cover;
    -webkit-background-size: cover;
    -moz-background-size: cover;
    }

    .menu-horizontal {
        width: 100%;
        display: inline-block;
    }
}

.pure-menu-horizontal{width:100%;white-space:nowrap}
.pure-menu-horizontal{display:inline-block}
.pure-menu-horizontal{display:inline-block;zoom:1;vertical-align:middle}
.pure-menu-horizontal{left:0;top:auto;width:inherit}
.pure-menu-horizontal{content:"\25BE"}
.pure-menu-horizontal.pure-menu-scrollable .pure-menu-list{display:inline-block}
.pure-menu-horizontal{white-space:nowrap;overflow-y:hidden;overflow-x:auto;-ms-overflow-style:none;-webkit-overflow-scrolling:touch;padding:.5em 0}
.pure-menu-horizontal.pure-menu-scrollable::-webkit-scrollbar{display:none}
.pure-menu-horizontal{background-color:#ccc;height:1px;margin:.3em 0}
.pure-menu-horizontal .pure-menu-separator{width:1px;height:1.3em;margin:0 .3em}
.pure-menu-horizontal .pure-menu-children .pure-menu-separator{display:block;width:auto}


